.contacts {
  @media only screen and (max-width: 767px) {
    text-align: center; }
  &__wrap {
    padding: 150px 0 65px;
    @media only screen and (max-width: 812px) {
      padding: 100px 50px; } }
  &__controls {
    &__title {
      font-family: 'Muller';
      font-weight: bold;
      font-size: 18px;
      line-height: 23px;
      padding-bottom: 5px; }
    p {
      margin-top: 5px; } }
  &__form {
    label, input {
      display: block; }
    label {
      text-align: left;
      @media only screen and (max-width: 767px) {
        text-align: center;
        width: 100%; }
      @include muller;
      font-size: 14px;
      line-height: 22px;
      padding: 15px 0; }
    input, textarea {
      width: 50%;
      border: 1px solid transparent;
      @media only screen and (max-width: 767px) {
        width: 70%;
        margin: 0 auto; }
      @media only screen and (max-width: 520px) {
        width: 100%; }
      &:hover {
        border: 1px solid #0cc; }
      &:focus {
        outline: none;
        border: 1px solid green; } }
    input {
      height: 30px; }
    textarea {
      height: 60px;
      resize: none; }
    &__button {
      margin: 30px 0 10px;
      background: #c00;
      height: 65px;
      width: 60%;
      font-family: "Muller";
      font-weight: bold;
      font-size: 18px;
      line-height: 40px;
      @media only screen and (max-width: 888px) {
        font-size: 14px; }
      @media only screen and (max-width: 767px) {
        margin: 0 auto; }
      @media only screen and (max-width: 520px) {
        width: 100%; }
      @media only screen and (max-width:350px) {
        font-size: 12px; } }
    span {
      @include muller;
      font-size: 13px;
      padding-left: 8%;
      @media only screen and (max-width: 394px) {
        font-size: 10px;
        padding-left: 0; } } }
  &__details {
    @include muller;
    font-size: 13px;
    color: black;
    &__title {
      padding: 20px 0;
      @media only screen and (max-width: 767px) {
        padding: 80px 0 20px; }
      h2 {
        text-align: left;
        @media only screen and (max-width: 767px) {
          text-align: center; } } }
    &__desc {
      line-height: 22px; }
    &__phone {
      font-weight: bold;
      font-size: 17px;
      line-height: 22px;
      color: black;
      text-decoration: none;
      &:hover {
        color: black;
        text-decoration: none; } }
    &__email {
      font-weight: normal;
      font-size: 17px;
      line-height: 22px;
      text-decoration: none;
      color: black;
      &:hover {
        text-decoration: underline;
        color: black; } }
    &__shedule {
      font-weight: normal;
      font-size: 17px;
      line-height: 22px;
      span {
        display: block; } } } }

.footer {
  @include muller;
  font-size: 13px;
  text-align: center;
  background: black;
  color: white;
  height: 135px;
  @media only screen and (max-width: 812px) {
    height: 80px; }
  @media only screen and (max-width: 767px) {
    font-size: 10px; }
  &__logo {
    padding-top: 45px;
    @media only screen and (max-width: 812px) {
      padding-top: 22px; }
    @media only screen and (max-width: 668px) {
      width: 100%;
      padding: 25px 35%; }
    @media only screen and (max-width: 394px) {
      padding: 30px 35%; }
    &__creator {
      padding-top: 35px;
      @media only screen and (max-width: 812px) {
        padding-top: 11px; }
      @media only screen and (max-width: 668px) {
        display: none; } } }
  &__text {
    padding-top: 36px;
    @media only screen and (max-width: 812px) {
      padding-top: 12px; }
    @media only screen and (max-width: 668px) {
      display: none; }
    p {
      line-height: 40px; } } }
